.card_container {
    width: 30rem;
    border-radius: 1rem;
    box-shadow: 0px 10px 8px #999;
    display: flex;
    flex-direction: column;
    margin: 0.5rem;
    padding: 0.5rem;
    background-color: white;
    height: fit-content;
}

.card_image {
    width: 100%;
    border-radius: 1rem 1rem 0 0;
}
.phone_number{
    margin: 0.5rem 5%;
}

.card_btn{
  text-align: center;
  width: 90%;
  border-radius: 5px;
 padding: 0.5rem 1rem;
 background-color: #2563eb;
 color: white;
 text-decoration: none;
 margin: 0.5rem;
}

.form {
    position: absolute;
    top: 20px;
    right: 30px;
}
.inputBox{
margin: 15px 220px 10px 25px;
}
.inputSet
{
    position: fixed;
    border-radius: 50px;
    border: 1px solid rgb(14, 11, 11);
    width: 200px;
    height: 20px;
    padding: 6px;
}
a{
    border: 1px solid black;
}
h2 {
    font-family:'Times New Roman', Times, serif;
}
p {
    font-family: 'Times New Roman', Times, serif;
    font-size: 24px;
}
ul {
   list-style-type: none;

}
li {
    padding: 5px;
}
select{
    border: 0;
    padding: 5px;
}
option{
   list-style-type: none;
}
.menu {
    position: fixed;
    top: 15px;
    right: 15px;
}
.icons{
    width: 50px;
    background-color: aliceblue;
    border-radius: 50%;
    border: 0;
}
a {border: 0;
}